//   color: #800080;
// }

//a:hover,a:active,a:focus {color:#c00; text-decoration:underline;}

/* 鼠标样式 */
input[type='submit'] {
  cursor: pointer;
}

button {
  cursor: pointer;
}

input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.clear {
  clear: both;
}
